import React, { useContext, useState } from 'react'
import { useNavigate } from 'react-router-dom';
import { UserContext } from '../context/UserContext';
import * as yup from 'yup';
import axios from 'axios';

const loginSchema = yup.object().shape(
  {
    email:yup.string().email().required(),
    password:yup.string().min(3).required()
  }
)


const LoginPage = () => 
{
  const navigate = useNavigate();
  const userContext = useContext(UserContext);
  const [email,setEmail] = useState('');
  const [password,setPassWord] = useState('');
  

  

  const handleFormSubmit = async (e) =>
  {    
    e.preventDefault();     

    try
    {
      let tempObj = 
      {
        email : email,
        password : password
      }

      const isValid = await loginSchema.isValid(tempObj);


      if(isValid)
      {
        // const savedUserResponse = await fetch(
        //   '/api/v1/auth/login',
        //   {
        //     method : "POST",
        //     headers : {"Content-Type" : "application/json"},
        //     body : JSON.stringify({email,password})
        //   }
        // );        
        // // console.log(savedUserResponse.status);
          
        // let savedUser;
        // if(savedUserResponse.status == 200)
        // {
        //   savedUser = await savedUserResponse.json(); 
        // }   

        const savedUser = await axios.post('/api/v1/auth/login',JSON.stringify({email,password}),
        {
          headers:{"Content-Type" : "application/json"}
        })
        
        
        if(savedUser.data.success)
        {
          userContext.setAuth(
            {
              ...userContext.auth,
              user:savedUser.data.user,
              token:savedUser.data.token
            }
          )
    
          localStorage.setItem('auth',JSON.stringify(savedUser.data));
        }
    
        navigate('/');
        window.location.reload();
      }
      else 
      {
        alert('invalid form entries');
        setEmail('');
        setPassWord('');
      }
    }
    catch(error)
    {
      console.log(error);
    }
  }

  const inputAreaStyle = 
  {
    padding:'0.5rem 1rem',
    border:'1px solid grey',
    borderRadius:'0.5rem',
    fontSize:'1rem' 
  }
  
  

  return (
    <div 
      style=
      {{
        display:'flex',
        height:'100vh',
        width:'100vw',
        alignItems:'center',
        justifyContent:'center'
      }}
    >
      <form 
        onSubmit={handleFormSubmit}
        style=
        {{
          display:'flex',
          flexDirection:'column',
          gap:'1.5rem',
          width:'18rem',
          alignItems:'center',
          justifyContent:'center'
        }}
      >
        <h1>Login Form</h1>
        <input 
          type="text" 
          name="email" 
          placeholder='Enter Email...' 
          value={email} onChange={(e)=>setEmail(e.target.value)}
          style=
          {inputAreaStyle}
        />

        <input 
          type="password" 
          name="password" 
          placeholder='Enter Password...' 
          value={password} 
          onChange={(e)=>setPassWord(e.target.value)}
          style={inputAreaStyle}
        />

        <button 
          type='submit' 
          style=
          {{
            padding:'0.5rem 1.5rem',
            borderRadius:'0.5rem',
            border:'1px solid grey',
            background:'transparent',
            fontSize:'1rem',
            cursor:'pointer'
          }}
        >
          Login
        </button>
        <p 
          style=
          {{
            color:'blue',
            fontWeight:'bold',
            cursor:'pointer'
          }}
          onClick={()=>navigate('/register')}
        >
          Don't Have an account ? Register Here.
        </p>
      </form>
    </div>
  )
}

export default LoginPage;