import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';
import * as yup from 'yup';
import axios from 'axios';

const registrationSchema = yup.object().shape(
  {
    name:yup.string().min(3).required(),
    profession:yup.string().required(),
    email:yup.string().email().required(),
    password:yup.string().min(3).required()
  }
)



const RegistrationPage = () => {

  const navigate = useNavigate();
  const [name,setName] = useState('');
  const [profession,setProfession] = useState('');
  const [email,setEmail] = useState('');
  const [password,setPassWord] = useState('');

  const inputAreaStyle = 
  {
    padding:'0.5rem 1rem',
    border:'1px solid grey',
    borderRadius:'0.5rem',
    fontSize:'1rem' 
  }

  const handleFormSubmit = async (e) =>
  {
    e.preventDefault();

    try
    {
      const tempObj = 
      {
        name : name,
        profession : profession,
        email : email,
        password : password
      }

      const isValid = await registrationSchema.isValid(tempObj);

      if(isValid)
      {
        // const savedUserResponse = await fetch(
        //   '/api/v1/auth/register',
        //   {
        //     method : "POST",
        //     headers : {"Content-Type" : "application/json"},
        //     body : JSON.stringify({name,profession,email,password})
        //   }
        // ); 

        const savedUserResponse = await axios.post('/api/v1/auth/register',JSON.stringify({name,profession,email,password}),
        {
          headers:{"Content-Type" : "application/json"}
        })
         
    
        navigate('/login');
      }
      else
      {
        alert('Invalid Input Entries');
        setName('');
        setPassWord('');
        setProfession('');
        setEmail('');
      }
    }
    catch(error)
    {
      console.error(error);
    }
  }


  return (
    <div 
      style=
      {{
        display:'flex',
        height:'100vh',
        width:'100vw',
        alignItems:'center',
        justifyContent:'center'
      }}
    >
      <form 
        onSubmit={handleFormSubmit}
        style=
        {{
          display:'flex',
          flexDirection:'column',
          gap:'1.5rem',
          width:'18rem',
          alignItems:'center',
          justifyContent:'center'
        }}
      >
        <h1>Registration Form</h1>
        <input 
          type="text" 
          name="name" 
          placeholder='Enter Name...' 
          value={name} onChange={(e)=>setName(e.target.value)}
          style=
          {inputAreaStyle}
        />

        <input 
          type="text" 
          name="profession" 
          placeholder='Enter Profession...' 
          value={profession} onChange={(e)=>setProfession(e.target.value)}
          style=
          {inputAreaStyle}
        />

        <input 
          type="text" 
          name="email" 
          placeholder='Enter Email...' 
          value={email} onChange={(e)=>setEmail(e.target.value)}
          style=
          {inputAreaStyle}
        />

        <input 
          type="password" 
          name="password" 
          placeholder='Enter Password...' 
          value={password} 
          onChange={(e)=>setPassWord(e.target.value)}
          style={inputAreaStyle}
        />

        <button 
          type='submit' 
          style=
          {{
            padding:'0.5rem 1.5rem',
            borderRadius:'0.5rem',
            border:'1px solid grey',
            background:'transparent',
            fontSize:'1rem',
            cursor:'pointer'
          }}
        >
          Register
        </button>
        <p 
          style=
          {{
            color:'blue',
            fontWeight:'bold',
            cursor:'pointer'
          }}
          onClick={()=>navigate('/login')}
        >
          Already have an account ? Login here.
        </p>
      </form>
    </div>
  )
}

export default RegistrationPage