import React , {useState,useContext} from 'react';
import '../AddTransaction/AddTransaction.css';
import {useMediaQuery} from "@mui/material";
import { TransactionContext } from '../../context/TransactionContext';



const AddTransaction = () => {

    const [transactionType , setTransactionType] = useState(true);
    const transactionContext = useContext(TransactionContext);
    let uid = JSON.parse(localStorage.getItem('auth')).user._id;
    const SmallScreens = useMediaQuery("(min-width : 700px)");
    const [type,setType] = useState('expense');
    const [amount,setAmount] = useState();
    const [description,setDescription] = useState('');

    const handleFormSubmit = async (e) =>
    {
        e.preventDefault();

        
        
        const newTransaction = await fetch(
            `/api/v1/transactions/create/${uid}`,
            {
                method :"POST",
                headers : {"Content-Type" : "application/json"},
                body : JSON.stringify({type,amount,description})
            }
        )

        const savedTransaction = await newTransaction.json();

        transactionContext.getAllTransactions();
        // onSubmitProps.resetForm();
        setAmount(0);
        setDescription('')
    }

    const inputAreaStyle = 
    {
        padding:'0.5rem 1rem',
        border:'1px solid grey',
        borderRadius:'0.5rem',
        fontSize:'1rem' 
    }

    

    
  return (
    <div 
        className='addTransactionContainer'
        style=
        {{
            width : !SmallScreens ? '18rem' : '40%'
        }}
    >
        <h3>Add Transaction</h3>
        <div className="transactionType">
            <div onClick={()=>{setTransactionType(true) ; setType('expense')}}>
                <h4 style={{color:'red'}}>Expense</h4>
                {transactionType && <div className='underline' style={{backgroundColor:"red"}}></div>}
            </div>
            <div onClick={()=>{setTransactionType(false); setType('income')}}>
                <h4 style={{color:'green'}}>Income</h4>
                {!transactionType && <div className='underline' style={{backgroundColor:"green"}}></div>}
            </div>
        </div>

        <form 
            onSubmit={handleFormSubmit}
            style=
            {{
            display:'flex',
            flexDirection:'column',
            gap:'1.5rem',
            // width:'18rem',
            alignItems:'center',
            justifyContent:'center'
            }}
        >
            <input 
                type="number" 
                name="amount" 
                placeholder='Enter Amount...' 
                value={amount} onChange={(e)=>setAmount(e.target.value)}
                style=
                {inputAreaStyle}
            />

            <input 
                type="text" 
                name="description" 
                placeholder='Enter Description...' 
                value={description} 
                onChange={(e)=>setDescription(e.target.value)}
                style={inputAreaStyle}
            />

            <button 
            type='submit' 
            style=
            {{
                padding:'0.5rem 1.5rem',
                borderRadius:'0.5rem',
                border:'1px solid grey',
                background:'transparent',
                fontSize:'1rem',
                cursor:'pointer'
            }}
            >
            Add
            </button>
        </form>

    </div>
  )
}

export default AddTransaction;